#welcomeToShop h1 { text-align: center; font-weight: lighter;font-family: Verdana, Geneva, Tahoma, sans-serif; font-style: italic; color: #9c75ff; margin-top: 30px; text-transform: uppercase}
#welcomeToShop h1 span {font-weight: bold; color: #61069e}
#content .productDetail img { width: 480px; height: 480px; }
#content .productDetail .left { float: left;}
#content .productDetail .right { float: right; width: 587px; margin-top: 24px}
#content .productDetail .right p { border-bottom: 1px solid rgba(0,0,0,.1); }
#content .productDetail .right span { text-decoration: underline}
#content .productDetail .right #productColor { margin-right: 235px; }
#content .productDetail .right select { border: 0.5px solid rgb(204, 197, 197); color:rgb(129, 122, 122); width: 350px; padding: 5px }
#content .productDetail .right #productSize { margin-right: 235px; margin-top: 10px; padding-top: 30px;}
#content .productDetail .right #productQuantity { padding-top: 30px;}
#content .productDetail .right #productQuantity input { text-align: center; width: 60px; padding: 1px}
#content .productDetail .right .buy a {margin-right: 40px; width: 220px; text-transform: uppercase; font-weight: bold;}
#content #description { margin-top: 50px}
#content #description a { font-weight: bold; text-transform: uppercase; color: #8014c9; letter-spacing: 2px}
#content #description #myTab, #myTabContent { margin-bottom: 50px}
#content #productImages img { text-align: center;margin:0 auto 20px;display: flex;}
#bannerWrapper {width: 1903px; }
.bannerForHer img {  background-size: cover; width: 951.5px; height: 970px;}
.bannerForHim img { background-size: cover; width: 951.5px; height: 970px;}
#bannerWrapper #resizeBannerHerHim { width: 1903px; margin: 0; }
#bannerWrapper #resizeMargin { margin: 0; }
#bannerWrapper #resizePadding { padding: 0px; }
#notFoundImg img { background-size: cover;width: 1903px; height: 734px;}
#notFoundImg h1 { text-align: center; color: rgb(221, 9, 9); font-weight: bold; letter-spacing: 5px}

.lds-spinner {
    color: official;
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
  }
  .lds-spinner div {
    transform-origin: 32px 32px;
    animation: lds-spinner 1.2s linear infinite;
  }
  .lds-spinner div:after {
    content: " ";
    display: block;
    position: absolute;
    top: 3px;
    left: 29px;
    width: 5px;
    height: 14px;
    border-radius: 20%;
    background: #cef;
  }
  .lds-spinner div:nth-child(1) {
    transform: rotate(0deg);
    animation-delay: -1.1s;
  }
  .lds-spinner div:nth-child(2) {
    transform: rotate(30deg);
    animation-delay: -1s;
  }
  .lds-spinner div:nth-child(3) {
    transform: rotate(60deg);
    animation-delay: -0.9s;
  }
  .lds-spinner div:nth-child(4) {
    transform: rotate(90deg);
    animation-delay: -0.8s;
  }
  .lds-spinner div:nth-child(5) {
    transform: rotate(120deg);
    animation-delay: -0.7s;
  }
  .lds-spinner div:nth-child(6) {
    transform: rotate(150deg);
    animation-delay: -0.6s;
  }
  .lds-spinner div:nth-child(7) {
    transform: rotate(180deg);
    animation-delay: -0.5s;
  }
  .lds-spinner div:nth-child(8) {
    transform: rotate(210deg);
    animation-delay: -0.4s;
  }
  .lds-spinner div:nth-child(9) {
    transform: rotate(240deg);
    animation-delay: -0.3s;
  }
  .lds-spinner div:nth-child(10) {
    transform: rotate(270deg);
    animation-delay: -0.2s;
  }
  .lds-spinner div:nth-child(11) {
    transform: rotate(300deg);
    animation-delay: -0.1s;
  }
  .lds-spinner div:nth-child(12) {
    transform: rotate(330deg);
    animation-delay: 0s;
  }
  .lds-spinner {
    position: relative;
    left: 500px;
    margin-top: 100px
  }
  @keyframes lds-spinner {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  

